.page-not-found__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.page-not-found__title {
  font-size: 70px;
}

.draggable {
  cursor: move;
  border: solid 1px rgba(0, 0, 0, 0.5);
  border-radius: 4px;
  background-color: #fafafa;
  padding: 0.5rem 1rem;
  margin-bottom: 8px;
}
.draggable.dragging {
  opacity: 0.5;
}

/* stylelint-disable no-duplicate-selectors */
/* stylelint-disable */
/* stylelint-disable declaration-bang-space-before,no-duplicate-selectors,string-no-newline */
.drop-target {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 128px;
  width: 100%;
  background-color: #fff;
  padding: 8px;
  float: left;
  border: 2px dashed rgba(0, 0, 0, 0.2);
  border-radius: 8px;
}
.drop-target.dragging {
  border-color: #1890ff;
}
.drop-target.hover {
  background-color: #fcfcfc;
}

.builder__container {
  display: flex;
  flex-direction: row;
}
.builder__container .ant-card {
  margin: 0;
}
.builder__container .survey {
  flex: 6 1;
  margin-right: 24px;
}
.builder__container .elements {
  flex: 2 1;
  align-self: flex-start;
}

.status {
  padding-left: 5px;
}
.status-tag--small {
  font-size: 10px;
  padding: 0 6px 10px;
}
.status-tag--medium {
  font-size: 13px;
  padding: 3px 7px 22px;
}
.status-tag--large {
  font-size: 15px;
  padding: 4px 9px 24px;
}

.row__title {
  display: flex;
  align-items: center;
}
.row__title .title {
  margin-right: 12px;
  margin-bottom: 0;
}
.row__title .icon--back {
  margin-right: 12px;
  font-size: 24px;
}

.ant-card {
  margin: 15px 0;
}
li {
  margin: 10px 0;
  padding: 0;
}

/* stylelint-disable no-duplicate-selectors */
/* stylelint-disable */
/* stylelint-disable declaration-bang-space-before,no-duplicate-selectors,string-no-newline */
.table-container {
  overflow-x: auto;
}
.table-scroll {
  min-width: 700px;
  max-width: 1400px;
  margin: 0 auto;
}
.action-buttons {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
span.filterText {
  padding-right: 6px;
}
.button {
  transition: color 0.3s ease;
}
.button--start:hover {
  color: #1890ff;
}
.button--details:hover {
  color: #faad14;
}
.button--delete:hover {
  color: #f5222d;
}

.editable-text--focused,
.editable-text span {
  outline: none;
}
.editable-text--focused {
  font-style: italic;
}
.editable-text .pencil {
  color: transparent;
  padding-left: 4px;
  cursor: text;
}
.editable-text span:focus .pencil,
.editable-text:hover .pencil {
  color: unset;
}

.layout {
  min-height: 100%;
}
.ant-layout-header,
.ant-layout-footer {
  background: #f9f9f9;
  z-index: 1;
}
.ant-layout-header {
  box-shadow: 0px 0px 4px 4px rgba(0, 0, 0, 0.05);
}
.ant-layout-footer {
  padding: 20px 50px;
}
.content {
  padding: 12px;
  min-height: 280px;
  height: 100%;
}
.content--wrapper {
  padding: 24px 48px 0;
  background: #fdfdfd;
}


body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
#root {
  height: 100%;
}
a {
  color: unset;
}

