.layout {
  min-height: 100%;
}

.ant-layout {
  &-header,
  &-footer {
    background: #f9f9f9;
    z-index: 1;
  }

  &-header {
    box-shadow: 0px 0px 4px 4px rgba(0, 0, 0, 0.05);
  }

  &-footer {
    padding: 20px 50px;
  }
}

.content {
  padding: 12px;
  min-height: 280px;
  height: 100%;

  &--wrapper {
    padding: 24px 48px 0;
    background: #fdfdfd;
  }
}

